.commonLoginBg-box {

    h1,
    p {
        margin: 0;
        padding: 0;
    }

    width: 100%;
    height: 100vh;
    overflow: hidden;
    position: relative;

    .left-top-logo {
        position: absolute;
        left: -2%;
        top: -6%;
        z-index: 1;
        transform: rotate(-15deg);
        width: 17.4vw;

    }

    .commonLoginBg-cnt {
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        align-items: center;

        .common-title {
            position: relative;
            top: 10%;
            text-align: center;
            font-family: PingFangSC-Light, PingFang SC;

            h1 {

                font-size: 64px;
                line-height: 90px;
            }

            p {
                font-size: 34px;
                line-height: 48px;
            }
        }

        .form-box {
            margin-top: 5%;
            width: 23.75vw;
            padding: 30px;
            background: rgba(255, 255, 255, 1);
            box-shadow: 0px 0px 16px 2px rgba(0, 0, 0, 0.2);
            border-radius: 10px;
        }

        .login-type {
            text-align: center;
        }
    }

    .commonLoginBg {
        position: absolute;
        left: -10%;
        top: -35%;
        width: 2000px;
        height: 1200px;
        background: linear-gradient(90deg, #eee 3%, transparent 0),
            linear-gradient(#eee 3%, transparent 0);
        background-size: 80px 80px;
        transform: rotate(-15deg);
    }
}